body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior-y: contain;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: white;
}

.link {
  background: #0004;
  border-radius: 1rem;
  padding: 1rem;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.arrows-link::before {
  content: ">";
}

.contact {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 12px;
}

.contact > a {
  position: relative;
  right: 2px;
  transition: right 1s;
}

.contact img {
  height: 64px;
  width: 64px;
  margin: 10px;
}

div.tint {
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: -1;
  transition: background-color 1s;
  content: "";
}

body::after {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  background-color: #282c34;
  content: "";
  background: linear-gradient(311deg, #1a1b1b, #045283);
  background-size: 400% 400%;

  -webkit-animation: scroll-background 13s ease infinite;

          animation: scroll-background 13s ease infinite;
}

@-webkit-keyframes scroll-background {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes scroll-background {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.justify-between {
  justify-content: space-between;
}

.justify-around {
  justify-content: space-around;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.height-100 {
  height: 100%;
}
.width-100 {
  width: 100%;
}

.p-2 {
  padding: 1rem;
}

.m-2 {
  margin: 1rem;
}

.px-2 {
  padding: 0 1rem;
}

.mx-2 {
  margin: 0 1rem;
}

.flex-1 {
  flex: 1 1;
}

img {
  width: 100%;
}

.contents {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.hidden {
  display: none;
}

.snap-item {
  scroll-snap-align: start;
}

.snap-container {
  -ms-scroll-snap-type: y mandatory;
  -webkit-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}

#root,
.App,
body,
html {
  height: 100%;
}

.red-bottom {
  border-bottom: 3px solid #660500;
}

